<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/attribute' }">属性管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="main-content">
      <!-- <p style="padding:0 0 10px 0;">{{breadcrumbTitle}}</p> -->
      <el-form
        :model="goodsinfo"
        :rules="ruleInfo"
        ref="goodsInfo"
        label-width="120px"
        class="demo-ruleForm"
      >
        <el-form-item label="属性名称：" prop="title">
          <el-input v-model="goodsinfo.title" style="width:372px;"></el-input>
        </el-form-item>
        <el-form-item label="选择属性值：">
          <el-select v-model="goodsinfo.type" placeholder="请选择">
            <el-option label="sku" value="sku">sku</el-option>
            <el-option label="extend" value="extend">extend</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="添加属性值：">
          <el-input type="textarea" :rows="5" v-model="goodsinfo.val" style="width: 300px"></el-input>
          <div>
            <p>
              <span style="color:red;">注意：</span>
              <i style="font-size:12px;">每个属性值间以英文逗号“,”间隔开，如：(“颜色：水泥灰,尺寸：XXXXXL,”)</i>
            </p>
          </div>
        </el-form-item>

        <el-form-item label="属性备注：">
          <el-input type="textarea" :rows="5" v-model="goodsinfo.remark" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmit('goodsInfo')">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import {
  APPKEY,
  APPSECRET,
  attributesIMforUrl,
  getAttributesIMforUrl
} from "../../../api/api";
import { findElem } from "../../../utils/fn";
import $ from "jquery";
export default {
  name: "",
  components: {},
  data() {
    return {
      breadcrumbTitle: "新增属性",
      requestMode :"post",
      attributesId:'',
      goodsinfo: {
        type: "",
        title: "",
        val: "",
        remark: ""
      },
      ruleInfo: {
        title: [{ required: true, message: "请输入属性名称", trigger: "blur" }]
      },
      value: []
    };
  },
  //计算属性
  computed: {},
  watch: {},
  created() {},
  mounted() {
    if (this.$route.query.id) {
      this.getPrevImfor();
    }
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    //   获取商品信息
    getPrevImfor() {
      if (this.$route.query.id) {
        this.breadcrumbTitle = "编辑属性";
        this.requestMode = "put";
        this.$ajax({
          method: "get",
          url: getAttributesIMforUrl + "/" + this.$route.query.id,
          params: {
            appKey: APPKEY,
            appSecret: APPSECRET
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        })
          .then(res => {
            if (res.status === 200) {
              let data = res.data.content;
              console.log(data);
              this.goodsinfo.title = data.title;
              this.goodsinfo.type = data.type;
              this.goodsinfo.val = data.val;
              this.goodsinfo.remark = data.remark;
            } else {
              this.$message.error("数据获取失败");
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    handleSubmit(name) {
      let val = this.goodsinfo.val.split(',').join(',');
      this.$refs[name].validate(valid => {
        if (valid) {
          let obj = {};
          if(this.requestMode ==="put"){
              this.attributesId = this.$route.query.id;
            }
          obj.appKey = APPKEY;
          obj.appSecret = APPSECRET;
          obj.title = this.goodsinfo.title;
          obj.type = this.goodsinfo.type;
          obj.val = val;
          obj.remark = this.goodsinfo.remark;
          this.postgoods(obj);
        } else {
          this.$message.error("请将信息填写完整!");
        }
      });
    },
    //新建商品
    postgoods(goods) {
      this.$ajax({
        method: this.requestMode,
        url: attributesIMforUrl+this.attributesId,
        data: goods
      })
        .then(res => {
          if (res.status === 200) {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            let postId = res.data.content.productId;
            console.log(postId);
            this.$router.push({
              path: "/store/attribute",
              query: {
                id: postId
              }
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style scoped lang="scss">
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}

.demo-upload-list img {
  width: 100%;
  height: 100%;
}

.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}

.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}

.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}

.item-form span {
  margin-right: 10px;
}

.item-specificatio-name,
.item-specificatio-value {
  margin-bottom: 15px;
}
.specification-box {
  border: 1px solid #d6d6d6;
  padding: 15px;
}
.item-specificatio-box > div {
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 18px;
}
.item-specificatio-box > span {
  margin-right: 10px;
}
.sku-value-list {
  justify-content: flex-start;
  align-items: center;
}
.sku-value-item {
  margin-right: 10px;
}
.sku-value-poptip {
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
}
.table-container {
  margin-top: 24px;
}
.img-w {
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
}
// 表格图片上传css
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 30px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
.attribute {
  width: 500px;
  display: inline-block;
  vertical-align: top;
}
</style>