<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/product-list' }">商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class>
      <el-steps :active="3" simple style="margin-top: 20px">
        <el-step title="1.基本信息" icon="el-icon-edit"></el-step>
        <el-step title="2.拓展信息" icon="el-icon-tickets"></el-step>
        <el-step title="3.商品详情" icon="el-icon-success"></el-step>
      </el-steps>
    </div>
    <div class="main-content">
      <div class="show-content">
        <p>商品详情效果预览</p>
        <div v-html="infoForm.a_content"></div>
        <p>商品详情效果预览</p>
      </div>
      <div>
        <el-form ref="infoForm" :model="infoForm">
          <el-form-item label>
            <div class="edit_container">
              <quill-editor
                class="editer"
                v-model="infoForm.a_content"
                ref="myQuillEditor"
                :options="editorOption"
                @blur="onEditorBlur($event)"
                @focus="onEditorFocus($event)"
                @change="onEditorChange($event)"
              ></quill-editor>
            </div>
          </el-form-item>
          <el-upload
            ref="upload3"
            :show-file-list="false"
            :on-success="handleSuccess3"
            :on-error="handleFormatError"
            action="https://upload.qiniup.com/"
            :data="{key:qnKey,token:qnToken}"
            style="display: none"
          >
            <input id="upload">
          </el-upload>
          <el-form-item>
            <el-button type="primary" @click="prevPage">上一步</el-button>
            <el-button type="primary" @click="onSubmit">确认提交</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import {APPKEY, APPSECRET, shopBaseUrl, qiniuTokenUrl, qiniuKeyUrl } from "../../../api/api";
import { quillEditor } from "vue-quill-editor";
import $ from "jquery";
import { setTimeout } from 'timers';

export default {
  name: "",
  components: { quillEditor },
  data() {
    return {
      breadcrumbTitle: "商品详情",
      defaultList: {},
      qnKey: "",
      qnToken: "",
      infoForm: {
        a_content: null
      },
      //编辑器
      editorOption: {
        placeholder: "编辑商品详情",
        modules: {
          //配置头部（功能）
          toolbar: [
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ list: "ordered" }, { list: "bullet" }],
            ["bold", "italic", "underline"],
            [{ size: ["small", false, "large", "huge"] }],
            [{ color: ["red", "blue"] }, { background: ["red", "blue"] }],
            [{ script: "sub" }, { script: "super" }],
            [{ direction: "rtl" }],
            ["code-block", "image"]
          ]
        }
      }
    };
  },
  methods: {
    prevPage(){
      this.$router.back(-1);
    },
    //   获取上一步信息
    getPrevImfor() {
      console.log(this.$route.query.id);
      this.$ajax({
        method: "get",
        url: shopBaseUrl + "/product/" + this.$route.query.id,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET
        }
      })
        .then(res => {
          if (res.status === 200) {
            let data = res.data.content;
            this.defaultList =data;
            
            this.infoForm.a_content = data.content;
            console.log(this.defaultList);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    onEditorReady(editor) {},
    onSubmit() {
      console.log(this.infoForm.a_content);
      this.defaultList.content = this.infoForm.a_content;
      this.defaultList.appKey = APPKEY;
          this.defaultList.appSecret = APPSECRET;
      this.$ajax({
        method: "put",
        url: shopBaseUrl + "/biz/product/" + this.$route.query.id,
        data: this.defaultList
      })
        .then(res => {
          if (res.status === 200) {
            this.$message({
              message: "提交成功",
              type: "success"
            });
            console.log(this.defaultList);
            setTimeout(()=>{this.$router.push("/store/product-list")},1000);
            
          }
        })
        .catch(error => {
          console.log(error);
        });
      
    },
    //上传图片
    handleView(url) {
      this.imgName = url;
      this.visible = true;
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: "文件格式错误",
        desc: "文件 " + file.name + "格式不正确, 请上传 jpg 或者 png格式图片."
      });
    },
    //获取七牛token,key,
    getTokenAandKey(tbRow) {
      if (tbRow) {
        this.tbIndex = tbRow.$index;
      }
      this.getqiniuToken();
      this.getqiniukey();
    },
    getqiniuToken() {
      this.$ajax({
        method: "get",
        url: qiniuTokenUrl
      })
        .then(res => {
          this.qnToken = res.data.content.upToken;
        })
        .catch(error => {
          console.error(error.response);
        });
    },
    getqiniukey() {
      this.$ajax({
        method: "get",
        url: qiniuKeyUrl
      })
        .then(res => {
          this.qnKey = res.data.content.key;
        })
        .catch(error => {
          console.log(error.response);
        });
    },

    //编辑器----------------------------
    onEditorBlur() {},
    onEditorFocus() {},
    onEditorChange() {},
    //点击图片触发事件
    imgClick() {
      this.getTokenAandKey();
      $("#upload").click();
    },
    //成功回调
    handleSuccess3(res, file, fileList) {
      this.$message.success("上传成功!");
      let img = "https://qiniu.easyapi.com/" + res.key;

      this.addImgRange = this.$refs.myQuillEditor.quill.getSelection();
      //        console.log(this.addImgRange)
      this.$refs.myQuillEditor.quill.insertEmbed(
        this.addImgRange != null ? this.addImgRange.index : 0,
        "image",
        img
      );
    }
  },

  created() {},
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  mounted() {
    this.getPrevImfor();
    this.uploadList3 = this.$refs.upload3.fileList;
    this.$refs.myQuillEditor.quill
      .getModule("toolbar")
      .addHandler("image", this.imgClick);
    //      this.getProductType()
    //      this.getProductSku()
  }
};
</script>
<style scoped lang="scss">
.show-content {
  width: 100%;
  margin: 30px auto;
}
.show-content > div {
  height: 550px;
  border: 1px solid #e5e5e5;
  overflow: auto;
}
.show-content > p {
  background: #e5e5e5;
  height: 38px;
  line-height: 38px;
  color: #999;
  text-align: center;
}
</style>