  methods: {
    // 获取列表
    getProductList(obj) {
      this.loading=true;
      if (this.active == 0) {
        obj.state = 1;
      } else if (this.active == 1) {
        obj.state = 0;
      } else if (this.active == 2) {
        obj.minQuantity = 0;
        obj.maxQuantity = 0;
      } else if (this.active == 3) {
        obj.ifWarn = true;
      }
      if (this.addTime.length != 0) {
        obj.startAddTime = this.addTime[0];
        obj.endAddTime = this.addTime[1];
      }
      obj.page = this.current - 1;
      obj.size = this.pageSize;
      this.$ajax({
        method: "get",
        url: sxproductUrl,
        params: obj
      })
        .then(res => {
          if (res.data.code === 0) {
            this.tableData = [];
          } else {
            // this.tableData = res.data.content;
            this.total = res.data.totalElements;
          }
          this.loading=false;
        })
        .catch(error => {
          console.log(error);
        });
