<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/product-list' }">商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class>
      <el-steps :active="2" simple style="margin-top: 20px">
        <el-step title="1.基本信息" icon="el-icon-edit"></el-step>
        <el-step title="2.拓展信息" icon="el-icon-tickets"></el-step>
        <el-step title="3.商品详情" icon="el-icon-success"></el-step>
      </el-steps>
    </div>
    <div class="main-content">
      <div class>
        <el-form ref="goodsinfo" :model="goodsinfo" label-width="150px">
          <!-- <div class="give-ub">
            <el-form-item label="赠送优币：">
              <el-input style="width:348px;" v-model="productExtendsObj.fieldValue"></el-input>
            </el-form-item>
            <p>购买该商品时赠送消费积分</p>
          </div>-->

          <el-form-item label="赠送等级成长值：">
            <el-input style="width:348px;" v-model="productExtendsObj.fieldValue"></el-input>
          </el-form-item>
          <el-form-item label="PV分成比例：">
            <el-input style="width:348px;"></el-input>%
          </el-form-item>
          <el-form-item label="U币抵扣比例：">
            <el-input style="width:348px;" v-model="goodsinfo.pointRate"></el-input>%
          </el-form-item>
        </el-form>
      </div>
      <el-row>
        <el-button type="primary" @click="prevPage">上一步</el-button>
        <el-button type="primary" @click="handleSubmit">下一步</el-button>
      </el-row>
    </div>
  </div>
</template>

<script>
import { APPKEY, APPSECRET, shopBaseUrl } from "../../../api/api";
export default {
  name: "",
  components: {},

  data() {
    return {
      breadcrumbTitle: "拓展信息",
      goodsinfo: {
        pointRate: 0,
        productExtends: []
      },
      postId: null,
      productExtendsObj: {
        fieldKey: "growUp",
        fieldName: "赠送等级成长值",
        fieldValue: 0
      }
    };
  },
  //计算属性
  computed: {},
  watch: {},
  created() {
    this.postId = this.$route.query.id;
  },
  mounted() {
    this.getPrevImfor();
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    prevPage() {
      this.$router.back(-1);
    },
    //   获取上一步信息
    getPrevImfor() {
      console.log(this.postId);
      this.$ajax({
        method: "get",
        url: shopBaseUrl + "/product/" + this.postId,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET
        }
      })
        .then(res => {
          if (res.status === 200) {
            let data = res.data.content;
            console.log(data);
            this.goodsinfo = data;
            this.pointRate = data.pointRate;
            let productExtendsArr = data.productExtends;
            this.goodsinfo.productExtends = productExtendsArr;
            console.log(productExtendsArr);
            for (let i=0;i<=productExtendsArr.length; i++){
              if(productExtendsArr[i].fieldKey == "growUp"){
                this.productExtendsObj.fieldKey = "growUp";
                this.productExtendsObj.fieldName = productExtendsArr[i].fieldName;
                this.productExtendsObj.fieldValue = productExtendsArr[i].fieldValue;
              }else{
                this.productExtendsObj.fieldKey = "growUp";
                this.productExtendsObj.fieldName = "赠送等级成长值";
                this.productExtendsObj.fieldValue = "";
              }
            }
            this.goodsinfo.productExtends.push(this.productExtendsObj);
            
            let ifRentV = findElem(productExtendsArr, "fieldKey", "ifRent");
            let depositV = findElem(productExtendsArr, "fieldKey", "deposit");
            if (ifRentV === -1) {
              this.goodsinfo.ifRent = "true";
              if (depositV === -1) {
                this.goodsinfo.deposit = 0;
              } else {
                this.goodsinfo.deposit = Number(
                  productExtendsArr[depositV].fieldValue
                );
              }
            } else {
              this.goodsinfo.ifRent = productExtendsArr[ifRentV].fieldValue;

              if (this.goodsinfo.ifRent == "true") {
                if (depositV === -1) {
                  this.goodsinfo.deposit = 0;
                } else {
                  this.goodsinfo.deposit = Number(
                    productExtendsArr[depositV].fieldValue
                  );
                }
              } else {
                this.goodsinfo.deposit = null;
              }
            }
            //商品缩略图
            let obj = {
              name: data.img,
              status: "finished",
              showProgress: false,
              url: data.img
            };
            this.uploadList.push(obj);
            //详情图
            if (data.imgs !== null) {
              let detailImgs = data.imgs.split(",");
              for (let i = 0; i < detailImgs.length; i++) {
                let obj = {
                  name: detailImgs[i],
                  status: "finished",
                  showProgress: false,
                  url: detailImgs[i]
                };
                this.uploadList2.push(obj);
              }
            }
            console.log(data);
            if (data.productSkus.length !== 0) {
              this.productSkus = data.productSkus;
              this.operSku();
            }

            if (this.goodsinfo.productSkus !== null) {
              this.enableSpec = true;
            } else {
              this.enableSpec = false;
              /* this.goodsinfo.price=this.productData.price;
                 this.goodsinfo.quantity=this.productData.quantity;*/
            }
            this.subordinateColumn = this.goodsinfo.productExtends[0].fieldValue;
            console.log(this.goodsinfo);
          } else {
            this.$Message.error("数据获取失败");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //提交执行下一步
    handleSubmit() {
      let arr = this.goodsinfo.productExtends
      arr.push(this.productExtendsObj);

      console.log(arr);
      this.goodsinfo.appKey = APPKEY;
      this.goodsinfo.appSecret = APPSECRET;
      // this.goodsinfo.pointRate = this.pointRate;
      this.$ajax({
        method: "put",
        url: shopBaseUrl + "/biz/product/" + this.$route.query.id,
        data: this.goodsinfo
      })
        .then(res => {
          if (res.status === 200) {
            console.log(this.goodsinfo);
            this.$message({
              message: "保存成功",
              type: "success"
            });
            this.$router.push({
              path: "/store/details",
              query: {
                id: this.$route.query.id
              }
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style scoped lang="scss">
.give-ub > p {
  width: 400px;
  text-align: center;
  font-size: 12px;
  color: #999999;
  letter-spacing: -0.29px;
  margin: -15px 0 20px 20px;
}
</style>