<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/brand' }">品牌管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="main-content">
      <!-- <p style="padding:0 0 10px 0;">{{breadcrumbTitle}}</p> -->
      <el-form
        :model="goodsinfo"
        :rules="ruleInfo"
        ref="goodsInfo"
        label-width="120px"
        class="demo-ruleForm">
        <el-form-item label="品牌名称：" prop="name">
          <el-input v-model="goodsinfo.name" style="width:360px;"></el-input>
        </el-form-item>
        <el-form-item label="品牌首字母：" prop="">
          <el-input v-model="goodsinfo.prefixLetter" style="width:360px;"></el-input>
        </el-form-item>
        <el-form-item label="品牌LOGO：">
          <div class="thumbnail-box flex-r">
            <el-upload
              class="avatar-uploader"
              ref="upload"
              :show-file-list="false"
              :on-success="handleSuccess"
              :on-error="handleFormatError"
              :before-upload="beforeAvatarUpload"
              action="https://upload.qiniup.com/"
              :data="{key:qnKey,token:qnToken}">
              <img class="avatar" :src="goodsinfo.logo" v-if="goodsinfo.logo">
              <i v-else class="el-icon-plus avatar-uploader-icon" @click="getTokenAandKey"></i>
            </el-upload>
          </div>
          <p style="color:#999;font-size:12px;">只能上传jpg/png格式文件，文件不能超过50KB</p>
        </el-form-item>
       <el-form-item label="品牌故事：">
          <el-input type="textarea" :rows="5" v-model="goodsinfo.description" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="是否显示：">
          <el-radio-group v-model="radio" @change="radioChange">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="2">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSubmit('goodsInfo')">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import {
  APPKEY,
  APPSECRET,
  qiniuTokenUrl,
  qiniuKeyUrl,
  brandsIMforUrl,
  getBrandsIMforUrl,
  getBrandsIMforUrl2
} from "../../../api/api";
import $ from "jquery";
export default {
  name: "",
  components: {},
  // props: {},
  data() {
    return {
      breadcrumbTitle: "新增品牌",
      imageUrl: "",
      upImgUrl: "",
      upImgUrlArr: [],
      brandId:"",
      radio: 1,
      requestMode :"post",
      value: "",
      options: [],
      goodsinfo: {
        name: "",
        ifShow: true,
        options: [],
        description: "",
        resource: "1",
        logo: "",
        prefixLetter: ""
      },
      uploadList: [], //缩略图
      ruleInfo: {
        name: [{ required: true, message: "请输入品牌名称", trigger: "blur" }]
      },
      // from backend
      specPrices: [],
      tbIndex: null, //选择上传图片的index
      qnKey: "",
      qnToken: "",
      value: [],
      list: [],
      loading: false,
    };
  },
  //计算属性
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.getPrevImfor();
    this.uploadList = this.$refs.upload.fileList;
    this.list = this.states.map(item => {
      return { value: item, label: item };
    });
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    //   获取商品信息
    getPrevImfor() {
      if (this.$route.query.id) {
        this.breadcrumbTitle = "编辑品牌";
        this.requestMode = "put";
        this.$ajax({
          method: "get",
          url: getBrandsIMforUrl2 + this.$route.query.id,
          params: {
            appKey: APPKEY,
            appSecret: APPSECRET
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        })
          .then(res => {
            if (res.status === 200) {
              let data = res.data.content;
              console.log(data);
              this.goodsinfo.name = data.name;
              this.goodsinfo.description = data.description;
              this.goodsinfo.prefixLetter = data.prefixLetter;
              this.goodsinfo.logo = data.logo;
              this.goodsinfo.ifShow = data.ifShow;
              if (data.ifShow === true) {
                this.radio = 1;
              } else {
                this.radio = 2;
              }
              //商品缩略图
              let obj = {
                name: data.img,
                status: "finished",
                showProgress: false,
                url: data.img
              };
              this.uploadList.push(obj);
            } else {
              this.$message.error("数据获取失败");
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    radioChange() {
      if (this.radio === 1) {
        this.goodsinfo.ifShow = true;
      } else {
        this.goodsinfo.ifShow = false;
      }
      console.log(this.goodsinfo.ifShow);
    },
    
    
    handleSubmit(name) {
      
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.uploadList.length === 0) {
            return this.$message.success("请上传商品图片");
          }
          let obj = {};
          if(this.requestMode ==="put"){
              this.brandId = this.$route.query.id;
            }
          obj.appKey = APPKEY;
          obj.appSecret = APPSECRET;
          obj.description = this.goodsinfo.description;
          obj.name = this.goodsinfo.name;
          obj.prefixLetter = this.goodsinfo.prefixLetter;
          obj.logo = this.uploadList[0].url;
          obj.ifShow = this.goodsinfo.ifShow;
          this.postgoods(obj);
        } else {
          this.$message.error("请将信息填写完整!");
        }
      });
    },
    
    //sku上传图片状态
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      let img = "https://qiniu.easyapi.com/" + res.key;
      file.url = img;
      file.name = file.name;
      this.upImgUrl = img;
      console.log(this.upImgUrl);
    },
    imgsuccess(res, file, fileList) {
      this.specPrices[this.tbIndex].prices.img =
        "http://qiniu.easyapi.com/" + res.key;
    },
    
    handleFormatError(file) {
      this.$Notice.warning({
        title: "文件格式错误",
        desc: "文件 " + file.name + "格式不正确, 请上传 jpg 或者 png格式图片."
      });
    },
    handleSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      let img = "https://qiniu.easyapi.com/" + res.key;
      this.goodsinfo.logo = img;
      file.url = this.goodsinfo.logo;
      this.upImgUrlArr.push(img);
      this.uploadList.push(file);
    },
    //获取七牛token,key,
    getTokenAandKey(tbRow) {
      if (tbRow) {
        this.tbIndex = tbRow.$index;
      }
      this.getqiniuToken();
      this.getqiniukey();
    },
    getqiniuToken() {
      this.$ajax({
        method: "get",
        url: qiniuTokenUrl
      })
        .then(res => {
          this.qnToken = res.data.content.upToken;
        })
        .catch(error => {
          console.error(error.response);
        });
    },
    getqiniukey() {
      this.$ajax({
        method: "get",
        url: qiniuKeyUrl
      })
        .then(res => {
          this.qnKey = res.data.content.key;
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    //新建商品
    postgoods(goods) {
      this.$ajax({
        method: this.requestMode,
        url: brandsIMforUrl+this.brandId,
        data: goods
      })
        .then(res => {
          if (res.status === 200) {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            let postId = res.data.content.productId;
            console.log(postId);
            this.$router.push({
              path: "/store/brand",
              query: {
                id: postId
              }
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //点击图片触发事件
    imgClick() {
      this.getTokenAandKey();
      $("#upload").click();
    }
    
  }
};
</script>
