<script>
import {
  APPKEY,
  APPSECRET,
  productOrderDetailUrl,
  productOrderCommentUrl,
  deliveryUrl,orderStepsUrl
} from "../../../api/api";
export default {
  name: "",
  components: {},
  data() {
    return {
      breadcrumbTitle: "订单详情",
      orderId: null,
      icons :'',
      orderState: "",
      showeapress: false,
      showExpr: true,
      orderComment: [],
      stepCurrentArr: [],
      wuliRadio: 1,
      showComment: false,
      value2: 5,
      commentImg: [],
      orderDetail: {},
      stepCurrent: 0, //当前步骤
      payState: "已付款", //支付状态
      deliverState: "已收货", //发货状态
      endState: "已完成", //最终收货状态

      deliverModal: false,
      formValidate: {
        name: "",
        no: "",
        postType: ""
      },
      ruleValidate: {
        name: [{ required: true, message: "请输入快递名称", trigger: "blur" }],
        no: [{ required: true, message: "请输入快递单号", trigger: "blur" }]
      }
    };
  },
  computed: {},
  watch: {},
  created() {
    this.orderId = this.$route.query.id;
  },
  mounted() {
    this.getOrderDetail();
    this.getProductOrderComment();
    this.getOrderSteps();
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    //   获取详情信息
    getOrderDetail() {
      this.$ajax({
        method: "get",
        url: productOrderDetailUrl + this.orderId,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET
        }
      })
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.orderDetail = res.data.content;
          }
          this.totalMoney = this.orderDetail.price - this.orderDetail.expressPrice;
          this.itemOrderPrice = this.orderDetail.price;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getOrderSteps(){
      this.$ajax({
        method: "get",
        url: orderStepsUrl,
        params: {
          productOrderId: this.orderId
        }
      })
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.stepCurrentArr = res.data.content;
            for(let i=0;i<this.stepCurrentArr.length; i++){
              this.stepCurrentArr[i].icon = "";
              if(this.stepCurrentArr[i].state == "waitPay"){
                this.stepCurrentArr[i].icon = "el-icon-shopping-cart-full";
              }else if(this.stepCurrentArr[i].state == "waitShopAccept"){
                this.stepCurrentArr[i].icon = "el-icon-time";
              }else if(this.stepCurrentArr[i].state == "shopHasAccept"){
                this.stepCurrentArr[i].icon = "el-icon-box";
              }else if(this.stepCurrentArr[i].state == "hasShopSend"){
                this.stepCurrentArr[i].icon = "el-icon-truck";
              }else if(this.stepCurrentArr[i].state == "waitComment"){
                this.stepCurrentArr[i].icon = "el-icon-sold-out";
              }else if(this.stepCurrentArr[i].state == "hasComment"){
                this.stepCurrentArr[i].icon = "el-icon-finished";
              }else if(this.stepCurrentArr[i].state == "hasCancel"){
                this.stepCurrentArr[i].icon = "el-icon-circle-close";
              }else if(this.stepCurrentArr[i].state == "hasFinish"){
                this.stepCurrentArr[i].icon = "el-icon-circle-check";
              }else if(this.stepCurrentArr[i].state == "waitShopSend"){
                this.stepCurrentArr[i].icon = "el-icon-sell";
              }
            }
            this.stepCurrent = res.data.content.length;
            console.log(this.stepCurrentArr);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取评论信息
    getProductOrderComment() {
      this.$ajax({
        method: "get",
        url: productOrderCommentUrl,
        params: {
          productOrderId: this.orderId
        }
      })
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.orderComment = res.data.content;
            if (orderComment.length != 0) {
              this.showComment = true;
              for (let key in this.orderComment) {
                this.commentImg = this.orderComment[key].imgs.split(",");
              }
            }

            console.log(this.commentImg);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // 是否显示物流输入框
    showExpress(val) {
      if (this.wuliRadio === 1) {
        this.formValidate.postType = 1;
        this.showExpr = true;
      } else {
        this.formValidate.postType = 0;
        this.showExpr = false;
      }
    },
    //3.提交发货信息
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$ajax({
            method: "put",
            url: deliveryUrl + this.orderId + "/delivery",
            data: {
              appKey: APPKEY,
              appSecret: APPSECRET,
              expressCompany: this.formValidate.name,
              expressNumber: this.formValidate.no
            }
          })
            .then(res => {
              console.log(res);
              this.$Message.success("发货成功!");
              this.deliverModal = false;
              this.handleReset("formValidate");
              this.getOrderList();
              // this.getAllOrderState();
            })
            .catch(error => {
              console.log(error.response);
            });
        } else {
          this.$message.error("请将信息填写完整!");
        }
      });
    },
    handleReset(name) {
      this.deliverModal = false;
      this.$refs[name].resetFields();
    },
    //接单
    joinOrder(id) {
      this.$confirm("您确定要接单吗？")
        .then(res => {
          this.$ajax({
            method: "put",
            url: deliveryUrl + id + "/accept",
            data: {
              appKey: APPKEY,
              appSecret: APPSECRET
            }
          })
            .then(res => {
              console.log(res);
              if (res.status === 200) {
                this.$message.success("操作成功");
                this.getOrderList(this.formInline);
                // this.getAllOrderState();
              }
            })
            .catch(error => {
              console.log(error.response.data.message);
            });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
